.card-warp {
  margin-bottom: 10px;
}
.card-warp button {
  margin-right: 10px;
}
.card-warp .setMarginBottom {
  margin-bottom: 10px;
}
.btn-group button {
  margin-right: 0;
}
/* modals */
/* use css to set position of modal */
.vertical-center-modal {
  text-align: center;
  white-space: nowrap;
}
.vertical-center-modal:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  width: 0;
}
.vertical-center-modal .ant-modal {
  display: inline-block;
  vertical-align: middle;
  top: 0;
  text-align: left;
}
