/* For demo */
.ant-carousel .slick-slide {
  text-align: center;
  height: 160px;
  line-height: 160px;
  background: #364d79;
  overflow: hidden;
}
.ant-carousel .slick-slide h3 {
  color: rgba(246, 250, 33, 0.966);
}
.slider-wrap .ant-carousel .slick-slide {
  height: 240px !important;
}
.slider-wrap .ant-carousel .slick-slide img {
  width: 100%;
}



